@import url(https://fonts.googleapis.com/css?family=Montserrat+Alternates);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: 'Montserrat Alternates', sans-serif;
  font-size: 20px;
  padding-top: 80px;
  background-color: #5f4c4c; }
  body .Game {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 1.5px;
    grid-template-rows: repeat(4, 1fr);
    width: 300px;
    height: 300px;
    margin: 0 auto;
    border: 4px solid #f46541;
    padding: 5px;
    overflow: hidden; }
    body .Game__Item {
      width: 100%;
      height: 100%;
      padding-top: 35%;
      border: 1px solid #f46541;
      border-radius: 5px;
      text-align: center;
      color: #fff;
      cursor: pointer; }
      body .Game__Item:hover {
        background-color: rgba(0, 0, 0, 0.3); }
    body .Game .stop {
      pointer-events: none; }
    body .Game .start {
      width: 100%;
      height: 100%;
      border-radius: 5px;
      background-image: url(https://raw.githubusercontent.com/olegobiukh/other/master/projects-general/img/barley-break/start.png);
      background-size: cover;
      background-color: #f46541;
      cursor: pointer; }
    body .Game .start__false {
      pointer-events: none; }
    body .Game .winner {
      width: 100%;
      height: 100%;
      background: #d87258;
      border-color: #fff;
      color: #fff;
      background-size: cover;
      pointer-events: none; }

